@use '../../../style/mixins';

:host {
    --example-controls-max-columns-width: 100%;
}

.avatar-container {
    @include mixins.is-resizable(
        $direction: horizontal,

        $min-width: 3rem,
        $height: auto,
        $min-height: 2rem
    );
    background-color: white;
}

limel-ai-avatar {
    --ai-avatar-core-blend-mode: normal;
    --ai-avatar-rings-blend-mode: normal;

    margin: auto;
    background-color: rgb(var(--contrast-100));

    &:hover {
        --ai-avatar-animation-play-state: running;
    }
}
